"use client"

import { useState } from "react"
import { Upload, Loader2, CheckCircle, AlertCircle } from "lucide-react"

export default function AjouterMorceau() {
  const [file, setFile] = useState<File | null>(null)
  const [uploading, setUploading] = useState(false)
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState("")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!file) {
      setError("Veuillez sélectionner un fichier audio")
      return
    }

    setUploading(true)
    setError("")
    setSuccess(false)

    const formData = new FormData()
    formData.append("file", file)

    try {
      const res = await fetch("/api/upload", {
        method: "POST",
        body: formData,
      })

      if (res.ok) {
        setSuccess(true)
        setFile(null)
        // Reset le input file
        const fileInput = document.querySelector('input[type="file"]') as HTMLInputElement
        if (fileInput) fileInput.value = ""
      } else {
        const data = await res.json()
        setError(data.error || "Erreur lors de l'upload")
      }
    } catch (err) {
      setError("Erreur réseau")
    } finally {
      setUploading(false)
    }
  }

  return (
    <div className="max-w-2xl mx-auto">
      <h2 className="text-3xl font-bold mb-6">Uploader de la musique</h2>
      
      <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-6">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Zone de drop */}
          <div className="border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg p-8 text-center">
            <Upload size={48} className="mx-auto mb-4 text-gray-400" />
            <input
              type="file"
              accept="audio/*"
              onChange={e => setFile(e.target.files?.[0] || null)}
              className="hidden"
              id="file-upload"
            />
            <label
              htmlFor="file-upload"
              className="cursor-pointer text-blue-600 hover:text-blue-700 font-medium"
            >
              Sélectionner un fichier audio
            </label>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-2">
              MP3, WAV, FLAC, etc.
            </p>
            {file && (
              <div className="mt-4 p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                <p className="font-medium text-blue-700 dark:text-blue-400">
                  {file.name}
                </p>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  {(file.size / 1024 / 1024).toFixed(2)} MB
                </p>
              </div>
            )}
          </div>

          {/* Messages */}
          {error && (
            <div className="flex items-center gap-2 p-4 bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-400 rounded-lg">
              <AlertCircle size={20} />
              <span>{error}</span>
            </div>
          )}

          {success && (
            <div className="flex items-center gap-2 p-4 bg-green-50 dark:bg-green-900/20 text-green-700 dark:text-green-400 rounded-lg">
              <CheckCircle size={20} />
              <span>Fichier uploadé avec succès ! Les métadonnées ont été extraites automatiquement.</span>
            </div>
          )}

          {/* Bouton submit */}
          <button
            type="submit"
            disabled={!file || uploading}
            className="w-full flex items-center justify-center gap-2 px-6 py-3 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed text-white rounded-lg font-medium transition-colors"
          >
            {uploading ? (
              <>
                <Loader2 size={20} className="animate-spin" />
                Upload en cours...
              </>
            ) : (
              <>
                <Upload size={20} />
                Uploader
              </>
            )}
          </button>
        </form>

        {/* Info */}
        <div className="mt-6 p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
          <h3 className="font-semibold mb-2 text-blue-900 dark:text-blue-300">
            ℹ️ Information
          </h3>
          <p className="text-sm text-gray-700 dark:text-gray-300">
            Les métadonnées (titre, artiste, album, genre, année) seront automatiquement extraites 
            du fichier si elles sont présentes. Vous pourrez les modifier ultérieurement.
          </p>
        </div>
      </div>
    </div>
  )
}
